import React, { useState } from 'react';
import DaumPostcodeEmbed from 'react-daum-postcode';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Axios from 'axios';
import '../App.css';
import { useNavigate } from 'react-router-dom';

function MemberForm(props) {
    const [openPostcode, setOpenPostcode] = React.useState(false);
    const [myid, setMyid] = useState('');
    const [mypass, setMypass] = useState('');
    const [myaddress, setMyaddress] = useState('');
    const [myaddress1, setMyaddress1] = useState('');
    const [myaddress2, setMyaddress2] = useState('');
    const [myname, setMyname] = useState('');
    const [btnok, setBtnok] = useState(false);
    const [open, setOpen] = React.useState(false);

    const navi=useNavigate();

    const handleClickOpen = () => {
        setOpen(true);
        setOpenPostcode(current => !current);
    };

    const handleClose = () => {
        setOpen(false);
        setOpenPostcode(false);
    };

    const handle = {
        //   
        selectAddress: (data) => {
            console.log(`
                : ${data.address},
                : ${data.buildingName}
                : ${data.zonecode}
            `)
            setMyaddress1(`(${data.zonecode})  ${data.address} ${data.buildingName}`);
            setOpen(false);
            setOpenPostcode(false);
        },
    }



    const onSubmitEvent = (e) => {
        e.preventDefault();//  
        if (!btnok) {
            alert("  !");
            return;
        }
        Axios.post("/member/insert", {myname, myid, mypass: `${myaddress1} ${myaddress2}`})
        .then(res=>{
            alert(".");
            navi("/login");
        })

    }
    //  
    const btnJungbok = () => {
        let url="member/searchid?myid="+myid;
        Axios.get(url)
        .then(res=>{
            if(Number(res.data)===0){
                alert("  .");
                setBtnok(true);
            }else{
                alert("   .");
                setMyid('');
                setBtnok(false);
            }
        })
    }
    return (
        <div>
            <form onSubmit={onSubmitEvent}>
                <table className='table' style={{ width: '500px' }}>
                    <caption align="top"><b></b></caption>
                    <tbody>
                        <tr>
                            <th style={{ width: '100px', backgroundColor: '#b0e0e6' }}></th>
                            <td>
                                <input type='text' className='form-control' placeholder='' required
                                    value={myname} onChange={(e) => setMyname(e.target.value)} />
                            </td>
                        </tr>
                        <tr>
                            <th style={{ width: '100px', backgroundColor: '#b0e0e6' }}></th>
                            <td className='input-group'>
                                <input type='text' className='form-control' placeholder='' required
                                    value={myname} onChange={(e) => {
                                        setMyid(e.target.value)
                                        setBtnok(false);
                                    }} />
                                <button type='button' className='btn btn-outline-danger btn-sm'
                                    onClick={btnJungbok}></button>
                            </td>
                        </tr>
                        <tr>
                            <th style={{ width: '100px', backgroundColor: '#b0e0e6' }}></th>
                            <td>
                                <input type='password' className='form-control'
                                    required style={{ width: '120px' }}
                                    value={mypass} onChange={(e) => setMypass(e.target.value)} />
                            </td>
                        </tr>
                        <tr>
                            <th style={{ width: '100px', backgroundColor: '#b0e0e6' }}></th>
                            <td>
                                <div className='input-group'>
                                    <input type='text' className='form-control' required
                                        readOnly value={myaddress1} />
                                    <button type='button' className='btn btn-sm btn-outline-success' onClick={handleClickOpen}></button>
                                </div>
                                <div>
                                    <input type='text' className='form-controle'
                                        value={myaddress2} onChange={(e) => setMyaddress2(e.target.value)}
                                        placeholder=' ' />
                                </div>
                            </td>
                        </tr>
                        <tr>
                            <td colSpan={2} align='center'>
                                <button tyoe='submit' className='btn btn-outline-info'
                                    style={{ width: '100px' }}></button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </form>
            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                    {openPostcode && 
                <DaumPostcodeEmbed
                    onComplete={handle.selectAddress}  //     
                    autoClose={false} //     DOM    
                    defaultQuery=' 235' //      
                    />}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose}>Disagree</Button>
                    <Button onClick={handleClose} autoFocus>
                        Agree
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}

export default MemberForm;